@media all and (max-width: 768px) and (min-width: 0) {
	.wrap {
		width: 100%;
		height: auto;
	}

	header {
		display: none;
	}
	.center {
		max-width: none;
		min-width: 100% !important;
	}
	.home {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		overflow: hidden;
		z-index: 0;
	}
	.home .intro {
		width: 100%;
		height: 100%;
	}

	.home .intro .name {
		font-size: 25px;
	}
	.home .intro .txt {
		font-size: 23px;
	}
	.home .intro .bar-1 {
		margin: 30px auto;
	}
	.home .intro p {
		font-size: 15.3x;
	}

	.about {
		min-width: 320px;
		width: 90%;
		height: auto;
		margin: 0 auto;
		overflow: hidden;
		min-width: none;
		max-width: none;
	}
	.about-center {
		width: 100%;
		height: auto;
		max-width: 768px !important;
		min-width: 0 !important;
	}

	.about-center {
		width: 100%;
		text-align: center;
	}
	.about-center .me {
		width: 100%;
		display: flex;
		justify-content: space-around;
	}

	.about-center .me .me-box {
		height: 100px;
		width: 50%;
		display: flex;
		justify-content: space-around;
	}
	.about-center .me .me-box .left {
		width: 20%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
	}
	.about-center .me .me-box .left img {
		width: 90%;
		margin: 0 auto;
	}

	.about-center .me .me-box .right {
		width: 70%;
		display: flex;
		margin: 0 auto;
		flex-flow: column nowrap;
		justify-content: center;
	}
	.about-center .me .me-box .right h3 {
		font-size: 15px;
	}
	.about-center .me .me-box .right p {
		font-size: 14 px;
	}

	.skill {
		width: 100%;
		min-width: none;
		max-width: none;
		height: auto;
		padding: 10px 0;
		box-sizing: border-box;
	}
	.skill h1 {
		width: 100%;
		padding: 0;
		line-height: 70px;
	}
	.skill .box {
		width: 100%;
		height: 1000px;
		display: flex;
		flex-flow: column nowrap;
	}

	.skill .skill-box {
		width: 100%;
		height: auto;
		display: flex;
		flex-flow: column nowrap;
	}

	.skill .skill-box .box {
		width: 90% !important;
		height: auto;
		margin: 20px auto;
	}
	.skill .skill-box .front-end h3 {
		width: 100%;
	}

	.skill .skill-box .front-end .skill-img {
		width: 100%;
		display: flex;
		overflow: row wrap;
	}
	.skill .skill-box .front-end .skill-img .react {
		width: 50%;
		height: 30%;
		object-fit: contain;
		padding: 20px;
		box-sizing: border-box;
	}
	.skill .skill-box .front-end .skill-img .jQuery {
		width: 50%;
		object-fit: contain;
	}

	.skill .skill-box .adobe .adobe-img {
		width: 33.33%;
	}

	.skill .skill-box .adobe .adobe-img img {
		width: 100%;
	}

	.project {
		width: 90%;
		margin: 0 auto;
		max-width: 100%;
	}

	.project-box {
		width: 100%;
		max-width: none;
		min-width: none;
	}

	.project .site-box {
		width: 90%;
		margin: 0 auto;
	}

	.site0 {
		display: flex;
		flex-flow: column nowrap;
	}

	.site0 .site {
		width: 90% !important;
		margin: 0 auto;
	}

	.site {
		overflow: hidden;
	}

	.site a {
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.site p {
		margin-top: 5px;
	}

	.project-box {
		width: 100%;
	}

	.project-box .project-react {
		width: 90% !important;
		max-width: none !important;
		min-width: none !important;
		margin: 20px auto;
		height: auto;
	}

	.project-box .project-react .project-sort {
		width: 100%;
		display: flex;
		justify-content: space-around;
	}

	.project-box .project-react .project-sort p {
		font-size: 10px;
		width: 100%;
		padding: 0;
	}

	.animate-project {
		width: 90%;
		height: auto;
		min-width: none;
		max-width: none;
	}
	.project-detail {
		width: 100%;
		display: flex;
		flex-flow: column nowrap;
		padding: 5px 0;
	}

	.project-detail .left {
		width: 90%;
		margin: 0 auto;
	}

	.project-detail .right {
		width: 90%;
		padding: 0;
		margin: 0 auto;
		height: auto;
	}

	.detail-right {
		width: 85%;
		overflow: hidden;
	}
	.detail-right a {
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.detail {
		position: relative !important;
		top: auto;
		left: auto;
		bottom: 0 !important;
	}
	.detail-left {
		width: 50px !important;
	}
	.detail-left h4 {
		width: 50px;
	}
	.animate-h1 {
		width: 100%;
		height: 50px;
		line-height: 50px !important;
	}
	.animate-project {
		padding: 10px 0;
	}
}
