.project {
	background-color: rgb(11, 9, 53);
	width: 100%;
	height: auto;
}

.project .site-box {
	color: black;
}

.project .site-box .site0 {
	display: flex;
	margin: 0 auto;
	justify-content: space-between;
}
.project .site-box h1 {
	width: 100%;
	text-align: center;
	line-height: 100px;
	color: white;
	font-weight: 900;
}
.project .site-box .bar {
	width: 60px;
	height: 0;
	border: 1px solid #fff;
	margin: 0 auto;
}

.project .site-box .site {
	width: 450px;
	height: 330px;
	background-color: white;
	margin: 40px auto 100px;
	border-radius: 20px;
	padding: 20px;
	box-sizing: border-box;
	position: relative;
	transition: all 1s;
	box-shadow: 10px 10px 20px #000;
	top: 0;
}

.project .site-box .site:hover {
	top: 10px;
}

.project .site-box .site1 img {
	width: 100px;
	height: 100px;
}
.project .site-box .site2 img {
	width: 70px;
	height: 50px;
	margin: 25px 0 23px 10px;
}
.project .site-box .site3 img {
	width: 70px;
	height: 70px;
	margin: 14px 0;
}

.project .site-box .site a {
	cursor: pointer;
	color: rgb(68, 110, 235);
}

.project .site-box .site p {
	line-height: 40px;
}

.project .site-box .site ul {
	color: black;
	top: 170px;
	left: 0;
}

.project .site-box .site ul li {
	padding: 0;
	font-weight: 450;
}

.project .site-box .site p span {
	font-weight: 700;
}

.project .project-box {
	width: 100%;
	height: auto;
	background-color: #333;
	padding-bottom: 100px;
}

.project .project-box > h1 {
	width: 100%;
	line-height: 100px;
	text-align: center;
	color: #fff;
	font-weight: 900;
}

.project .project-box .bar {
	width: 220px;
	height: 0;
	border: 1px solid white;
	margin: 0 auto;
}

.project-react {
	overflow: hidden;
	width: 1000px;
	height: 500px;
	background-color: #fff;
	margin: 50px auto;
	padding-bottom: 40px;
	border-radius: 20px;
	box-shadow: 10px 10px 20px #000;
}
.project-react .project-sort {
	width: 100%;
	margin: 0 auto;
	display: flex;
	/* justify-content: center; */
}

.project-react .project-sort p {
	width: 200px;
	line-height: 40px;
	font-weight: 700;
	font-size: 18px;
	text-align: center;
	padding: 10px;
}

.project-detail {
	width: 90%;
	margin: 10px auto;
	display: flex;
	box-sizing: border-box;
	overflow: hidden;
	flex-flow: row wrap;
}

.project-detail h1 {
	display: block;
	width: 100%;
	text-align: center;
	color: black;
	margin-bottom: 30px;
}

.project-detail .left {
	width: 50%;
	height: auto;
}

.project-detail .left .img-box {
	width: 100%;
	height: 350px;
	overflow: hidden;
}
.project-detail .left .img-box img {
	width: 100%;
	height: 350px;
	object-fit: cover;
}
.project-detail .right {
	width: 50%;
	height: auto;
	padding: 0 20px;
	box-sizing: border-box;
	position: relative;
}
.project-detail .right p {
	width: 100%;
	font-size: 17px;
	font-weight: 600;
	text-align: left;
}
.project-detail .right .detail {
	position: absolute;
	width: 100%;
	display: flex;
	bottom: 100px;
	text-align: left;
}
.project-detail .right .detail-left {
	width: 20%;
}
.project-detail .right .detail-right a {
	text-decoration: underline;
	color: rgb(68, 110, 235);
}

.project-sort p.selected {
	border-bottom: 2px solid black;
	background-color: #ebebeb;
}

.bar2 {
	width: 300px;
	height: 0;
	border: 1px solid black;
	margin: 0 auto;
}

.animate-project {
	width: 1000px;
	margin: 10px auto;
	background-color: #fff;
	border-radius: 20px;
	height: 400px;
}

.animate-project .animate-h1 {
	color: black !important;
	text-align: center;
	padding: 20px 0;
}

.animate-project .project-detail {
	height: auto;
	margin: 0 auto;
}

.animate-project .project-detail .left .img-box {
	height: 250px;
	overflow: hidden;
}

.animate-project .project-detail .left .img-box img {
	height: 250px;
}

.animate-project .project-detail .right .detail {
	bottom: 50px;
}

.swiper {
	width: 100%;
	height: 100%;
	position: relative;
}

.swiper-slide {
	text-align: center;
	font-size: 18px;
	background: #fff;

	/* Center slide text vertically */
	display: flex;
	justify-content: center;
	align-items: center;
}

.swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.swiper-pagination-bullet-active {
	background-color: black;
}
